import React from 'react'

import TitleText from '../components/TitleText'

import {
  Image,
  Row,
  Col,
} from 'antd'

const ImageColumn = ({ item, lr }) => (
  <Col className='aitt-img-col'>
    <Image
      className='aitt-img'
      src={item.image}
      fallback="data:image/png;base64,"
    />
  </Col>
)

const ContentColumn = ({ item, lr }) => (
  <Col className={lr === 'imgText' ? 'aitt-content-col-img-text' : 'aitt-content-col-text-img'}>
    <TitleText
      title={item.title}
      titleStyle={{ textAlign: 'left', fontSize: '1.6rem' }}
      text={item.writeup}
      textStyle={{ textAlign: 'justify', lineHeight: '30px', fontSize: '1rem', fontWeight: '200', margin: 0 }}
    />
  </Col>
)

const rowStyle = (i) => {
  return {
    backgroundColor: '#f4f4f4',
    margin: i % 2 === 0 ? '60px 15% 60px 0' : '60px 0 60px 15%',
  }
}

const titleUnderlineStyle = {
  display: 'flex',
  justifyContent: 'center',
}

const ServicePage = (props) => {
  const {
    currService,
    currServiceData
  } = props

  if (currServiceData.length === 0) {
    return <div></div>
  }

  return (
    <div className='service-page-container'>
      {/* <TitleText titleUnderlineStyle={titleUnderlineStyle} title={currServiceData[0].congre} underlineAlign='center' /> */}
      {currServiceData.map((item, i) =>
        <Row key={i} className='aitt-row' style={rowStyle(i)}>
          {
            i % 2 === 0
              ?
              (<>
                <ImageColumn item={item} lr='imgText' />
                <ContentColumn item={item} lr='imgText' />
              </>)
              :
              (<>
                <ContentColumn item={item} lr='textImg' />
                <ImageColumn item={item} lr='textImg' />
              </>)
          }
        </Row>
      )}
    </div >
  )
}

export default ServicePage